import React from 'react'
import './App.css'
import { BrowserRouter, Route } from 'react-router-dom'
import { tree, tree2 } from './Tree'
import Chart from './components/Chart'

export default class App extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      tree: null,
    }
  }
  render() {
    return (
      <>
        <button onClick={() => this.setState({ tree: tree })}>Tree 1</button>
        <button onClick={() => this.setState({ tree: tree2 })}>Tree 2</button>
        {this.state.tree &&
          <Chart tree={this.state.tree} />
        }
        {/* {!_.isEmpty(this.state.tree) && this.state.tree.id === 100 &&
          <Chart tree={this.state.tree} />
        }
        {!_.isEmpty(this.state.tree) && this.state.tree.id === 300 &&
          <Chart tree={this.state.tree} />
        } */}
      </>
    )
  }
}
